.data-purchase {
  padding: 70px 0;

  &__container {
    overflow: hidden;
  }

  &__header {
    display: flex;
    justify-content: center;

    @include max(sm) {
      justify-content: flex-start;
    }
  }

  &__dots {
    margin-top: 35px;
    overflow: visible;

    .swiper-wrapper {
      justify-content: center;

      @include max(sm) {
        justify-content: flex-start;
      }
    }
  }

  &__dot {
    max-width: 105px;
    padding: 10px 23px;

    &:not(:last-child) {
      margin-right: 78px;

      @include max(sm) {
        margin-right: 58px;
      }
    }
  }

  &__slider {
    justify-content: center;
    margin-top: 35px;

    .swiper-button-prev,
    .swiper-button-next {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 46px;
      height: 46px;
      cursor: pointer;
      z-index: 2;

      @include max(md) {
        display: none;
      }

      svg {
        pointer-events: none;
        fill: #6e6b7b;
      }
    }

    .swiper-button-next {
      right: 0;
    }
  }

  &__slide {
    display: flex;
    justify-content: center;
    padding: 0 45px;

    @include max(md) {
      padding: 0;
    }

    @include max(sm) {
      flex-direction: column-reverse;
    }

    &-content {
      max-width: 388px;
      margin-right: 27px;
      padding-bottom: 80px;
      background-image: url("../images/page-3/slide-content-bg.svg");
      background-repeat: no-repeat;
      background-position: bottom left;
      background-size: auto;

      @include max(lg) {
        flex-basis: 45%;
      }

      @include max(sm) {
        flex-basis: 100%;
        width: 100%;
        max-width: 100%;
        background-size: 100%;
        margin-top: 30px;
        padding-bottom: 24.7vw;
        margin-right: 0;
      }
    }

    &-title {
      color: $dark-gray;
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
    }

    &-text {
      font-weight: normal;
      font-size: 14px;
      line-height: 21px;
      color: $dark-gray-6e;
      margin-top: 22px;
    }

    &-image {
      position: relative;

      &-text {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        max-width: 100px;
        font-size: 10px;
        line-height: calc(12 / 10);
        font-weight: 700;
        color: $white;
        text-align: center;

        @include max(1270) {
          max-width: vw(100, 1270);
          font-size: vw(10, 1270);
        }

        @include max(sm) {
          max-width: vw(100, 767);
          font-size: vw(10, 767);
        }

        &--connect {
          max-width: 78px;
          top: 26%;
          left: 49%;

          @include max(1270) {
            max-width: vw(78, 1270);
          }

          @include max(md) {
            left: 49.5%;
            top: 26.5%;
          }

          @include max(sm) {
            max-width: vw(78, 767);
          }
        }

        &--monitor {
          top: 65.5%;
          left: 42%;

          @include max(md) {
            left: 42.5%;
            top: 66%;
          }
        }
      }

      @include max(lg) {
        flex-basis: 55%;
        align-self: center;
      }

      @include max(sm) {
        flex-basis: 100%;
        width: 100%;
      }

      img {
        //object-fit: cover;
        width: 100%;
        height: auto;
      }
    }
  }
}
