.about-nodes {
  padding: 49px 0 70px;

  @include max(sm) {
    padding: 40px 0;
    overflow: hidden;
  }

  &__container {
  }

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 34px;

    @include max(sm) {
      display: block;
      margin-bottom: 42px;
    }
  }

  &__text {
    position: relative;
    z-index: 1;
    max-width: 920px;
    margin: 0 auto;

    font-size: 14px;
    color: $dark-gray-6e;
    line-height: calc(21 / 14);
    text-align: center;

    @include max(sm) {
      margin-bottom: 44px;
      text-align: left;
    }

    strong,
    b {
      font-weight: 600;
    }
  }

  &__scene {
    $scene: &;
    position: relative;
    margin-top: -13px;

    &--opened {
      @include max(sm) {
        #{$scene}-outer {
          display: none;
        }

        #{$scene}-inner {
          display: block;
        }
      }
    }

    &-outer {
      @include max(sm) {
        overflow: hidden;
        //display: none;

        &.hide {
          display: none;
        }
      }

      img {
        @include max(sm) {
          position: relative;
          left: 50%;
          transform: translateX(-52.7%);
          max-width: none;
          width: 169.1318327974277%;
          height: auto;
        }
      }
    }

    &-inner {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1001;
      //background-color: rgba(yellow, 0.4);
      margin-top: -60px;
      transition: opacity 0.3s ease;

      @include max(sm) {
        position: relative;
        z-index: 1;
      }

      &-wrapper {
        @include max(sm) {
          position: relative;
          width: calc(100% + 64px);
          margin-left: -32px;
        }
      }

      img:not(.about-nodes__zoom-image) {
        @include max(sm) {
          position: relative;
          left: 50%;
          transform: translateX(-53.2%);
          max-width: none;
          width: 144.4%;
          height: auto;
        }
      }

      map {
        @include max(sm) {
          display: none;
        }
        area {
          cursor: pointer;
          @include max(sm) {
            display: none;
          }
        }
      }

      &.hide {
        opacity: 0;
        pointer-events: none;

        @include max(sm) {
          display: none;
          pointer-events: auto;
          opacity: 1;
        }
      }
    }

    &-dot {
      position: absolute;
      z-index: 1003;
      border: none;
      padding: 0;
      appearance: none;
      cursor: pointer;
      //background-color: rgba(yellow, 0.2);

      &::before,
      &::after {
        content: "";
        position: absolute;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $white;
      }

      &::after {
        animation-name: pulseHint;
        animation-duration: 2s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
      }

      &--guards {
        top: 47%;
        left: 27.3%;
        width: 10%;
        height: 45.4%;

        &::before,
        &::after {
          top: 91%;
          left: 30%;
        }

        @include max(sm) {
          height: 49%;
          width: 21.6%;
          left: 5%;
          top: 43.2%;

          &::before,
          &::after {
            top: 50%;
            left: 50%;

            background-color: $citrus;
          }
        }
      }

      &--acceptance {
        top: 26.1%;
        left: 38.6%;
        width: 24%;
        height: 9.2%;

        &::before,
        &::after {
          top: 50%;
          left: 12.5%;
        }

        @include max(sm) {
          height: 18.1%;
          width: 57.3%;
          left: 20.7%;
          top: 22%;

          &::before,
          &::after {
            top: 26%;
            left: 21%;
          }
        }
      }

      &--legal {
        top: 36.1%;
        left: 38.6%;
        width: 24%;
        height: 11.9%;

        &::before,
        &::after {
          top: 70%;
          left: 89%;
        }

        @include max(sm) {
          height: 9.4%;
          width: 14.1%;
          left: 59.6%;
          top: 36%;

          &::before,
          &::after {
            top: 50%;
            left: 52%;
          }
        }
      }

      &--processing {
        top: 48.7%;
        left: 50.6%;
        width: 8.4%;
        height: 6.8%;

        &::before,
        &::after {
          top: 48%;
          left: 50%;
        }

        @include max(sm) {
          height: 6.9%;
          width: 11.2%;
          left: 51.5%;
          top: 45.7%;

          &::before,
          &::after {
            top: 46%;
            left: 52%;
          }
        }
      }

      &--analytical {
        top: 56.6%;
        left: 38.6%;
        width: 24%;
        height: 5.9%;

        &::before,
        &::after {
          top: 63%;
          left: 85%;
        }

        @include max(sm) {
          height: 7.8%;
          width: 51.2%;
          left: 23.5%;
          top: 52.6%;

          &::before,
          &::after {
            top: 50%;
            left: 50%;
          }
        }
      }

      &--segmentation {
        top: 63%;
        left: 38.6%;
        width: 24%;
        height: 8.4%;

        &::before,
        &::after {
          top: 23%;
          left: 89%;
        }

        @include max(sm) {
          height: 10.3%;
          width: 51.3%;
          left: 23.5%;
          top: 59.2%;

          &::before,
          &::after {
            top: 50%;
            left: 50%;
          }
        }
      }

      &--storage {
        top: 72.2%;
        left: 38.6%;
        width: 24%;
        height: 6.7%;

        &::before,
        &::after {
          top: 38%;
          left: 32.2%;
        }

        @include max(sm) {
          height: 10.3%;
          width: 51.3%;
          left: 23.5%;
          top: 68.4%;

          &::before,
          &::after {
            top: 50%;
            left: 50%;
          }
        }
      }

      &--bank {
        top: 83.2%;
        left: 41.3%;
        width: 19.3%;
        height: 12.7%;

        &::before,
        &::after {
          top: 68%;
          left: 20%;
        }

        @include max(sm) {
          height: 14.2%;
          width: 51.3%;
          left: 23.5%;
          top: 81%;

          &::before,
          &::after {
            top: 50%;
            left: 50%;
          }
        }
      }
    }

    &-zoom {
      display: none;

      @include max(sm) {
        display: block;
        position: absolute;
        top: 22%;
        left: 20.7%;
        width: 57.3%; //215px;
        height: 18.1%; //91px;
        border: 4px solid $citrus;
        border-radius: 2px;

        &--guards-1 {
          display: none;
        }
        &--guards-2 {
          display: none;
        }
      }
    }
  }

  &__zoom {
    display: none;

    @include max(sm) {
      display: block;
      position: relative;
      width: 100%;
      margin: 12px auto 36px;
      padding-top: 65%;
      box-shadow: 0px 0px 19px 3px rgba(0, 0, 0, 0.17);
      border-radius: 6px;
      overflow: hidden;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      border-radius: 6px;
      border: 8px solid $citrus;
    }

    &-image {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
      width: 100% !important;
      height: auto;
      transform: translateX(0) !important;

      &.active {
        z-index: 1;
      }
    }
  }

  &__carousel {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1004;
    overflow: visible;
    width: 100%;
    height: 100%;

    pointer-events: none;

    @include max(sm) {
      position: relative;
      height: auto;
      pointer-events: auto;

      &::before {
        content: "";
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        transform: translateX(-100%);
        width: 100%;
        height: 100%;
        background-color: $white;
      }
    }

    .swiper-wrapper {
      pointer-events: none;

      @include max(sm) {
        pointer-events: auto;
      }
    }

    .swiper-navigation {
      display: none;

      @include max(sm) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 24px;
      }

      button {
        cursor: pointer;
        appearance: none;

        @include max(sm) {
          padding-left: 0;
          padding-right: 0;
        }

        svg {
          fill: $gray-97;
          transition: fill 0.3s ease;
        }

        @include hover {
          svg {
            fill: $citrus;
          }
        }
      }

      .swiper-button-prev {
        svg {
          transform: scale(-1, 1);
        }
      }

      .swiper-button-disabled {
        opacity: 0;
        pointer-events: none;

        @include max(sm) {
          display: none;
        }
      }
    }
  }

  &__item {
    $item: &;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    display: flex;
    width: auto;
    height: auto;
    pointer-events: auto;
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: 0 0;
    box-sizing: border-box;
    pointer-events: none;
    transition: opacity 0.3s ease;

    @include max(sm) {
      position: static;
      max-width: 211px;
      margin-right: 12px;

      &:not(.swiper-slide-active) {
        opacity: 0.51 !important;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    &-inner {
      flex-grow: 1;
      pointer-events: auto;
    }

    &.hide {
      opacity: 0;

      @include max(sm) {
        opacity: 1;
      }

      #{$item}-inner {
        pointer-events: none;

        @include max(sm) {
          pointer-events: auto;
        }
      }
    }

    &--guards {
      top: 31%;
      left: 4%;
      width: 349px;
      height: 570px;
