    &--acceptance,
    &--legal,
    &--processing,
    &--analytical,
    &--segmentation,
    &--storage,
    &--bank {
      @include max(sm) {
        width: auto;
        height: auto;
        padding: 0;
        background-image: none;

        @include retina {
          background-image: none;
        }

        #{$item}-text {
          max-width: none;
        }

        #{$item}-inner {
          padding: 0;
        }
