      font-size: 14px;
      line-height: calc(17 / 14);
      color: $white;
      font-weight: 700;
      text-transform: uppercase;

      @include max(1270) {
        margin-bottom: vw(11, 1270);
        font-size: vw(14, 1270);
      }

      @include max(sm) {
        margin: 0;
        font-size: 12px;
        line-height: calc(18 / 12);
        color: $dark-gray-6e;
        text-transform: none;
      }
    }

    &-text {
      font-size: 8px;
      line-height: calc(11 / 8);
      color: $white;
      font-weight: 500;

      @include max(1270) {
        font-size: vw(8, 1270);
      }

      @include max(sm) {
        font-size: 12px;
        line-height: calc(18 / 12);
        color: $dark-gray-6e;
        font-weight: 400;
